</template>

<script>
  import headernav from "./components/top"

  export default {
    name: 'App',
    components: {
      headernav: headernav
    },
    data() {
      return{
      }
    },
  }
</script>

<style>
  .el-header, .el-footer {
    text-align: left;
  }
  .el-aside {
    text-align: left;
  }

  .el-main {
    overflow: hidden;
    text-align: left;
  }

  /*! CSS Used from: Embedded ; media=screen */
  @media screen{
    [data-darkreader-inline-fill]{fill:var(--darkreader-inline-fill)!important;}
  }
  /*! CSS Used from: Embedded ; media=screen */
  @media screen{
    a{color:#4791e6;}
    ::placeholder{color:#bab3a6;}
    ::selection{background-color:#1760b9;color:#fbf7f1;}
    ::-moz-selection{background-color:#1760b9;color:#fbf7f1;}
    ::-webkit-scrollbar{background-color:#272928;color:#c4beb3;}
    ::-webkit-scrollbar-thumb{background-color:#353536;}
    ::-webkit-scrollbar-thumb:hover{background-color:#3c3e3e;}
    ::-webkit-scrollbar-thumb:active{background-color:#464848;}
    ::-webkit-scrollbar-corner{background-color:#242525;}
    *{scrollbar-color:#353536 #272928;}
  }
  /*! CSS Used from: https://unpkg.com/docsify/lib/themes/vue.css */
  *{-webkit-font-smoothing:antialiased;-webkit-overflow-scrolling:touch;-webkit-tap-highlight-color:rgba(0,0,0,0);-webkit-text-size-adjust:none;-webkit-touch-callout:none;box-sizing:border-box;}
  .github-corner{border-bottom:0;position:fixed;right:0;text-decoration:none;top:0;z-index:1;}
  .github-corner:hover .octo-arm{animation:a .56s ease-in-out;}
  .github-corner svg{color:#fff;fill:var(--theme-color,#42b983);height:80px;width:80px;}
  @media print{
    .github-corner{display:none;}
  }
  @media screen and (max-width:768px){
    .github-corner{position:fixed;}
    .github-corner{transition:transform .25s ease-out;}
    .github-corner:hover .octo-arm{animation:none;}
    .github-corner .octo-arm{animation:a .56s ease-in-out;}
  }
  /*! CSS Used from: Embedded ; media=screen */
  @media screen{
    *{-webkit-tap-highlight-color:rgba(229, 224, 216, 0);}
    .github-corner{border-bottom-color:initial;text-decoration-color:initial;}
    .github-corner svg{color:rgb(251, 247, 241);fill:rgb(137, 202, 166);}
  }
  /*! CSS Used keyframes */
  @keyframes a{0%,to{transform:rotate(0);}20%,60%{transform:rotate(-25deg);}40%,80%{transform:rotate(10deg);}}

</style>



